// src/App.js
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import ConsultaForm from './components/ConsultaForm';
import ResultadoConsulta from './components/ResultadoConsulta';
import RelatorioForm from './components/RelatorioForm';
// --- (NOVA IMPORTAO) ---
import IngestaoForm from './components/IngestaoForm'; 
import Header from './components/Header';
// --- (NOVA IMPORTAO) ---
import { consultarAPI, testarConexao, ingestarRepositorio } from './services/api';
import './App.css';
// --- (Styled Components... no mude nada neles) ---
const AppContainer = styled.div`
  width: 400px;
  min-height: 500px;
  max-height: 600px;
  overflow-y: auto;
  padding: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
`;

const Tabs = styled.div`
  display: flex;
  margin-bottom: 20px;
  border-bottom: 1px solid #e1e4e8;
`;

const Tab = styled.button`
  padding: 10px 15px;
  background: ${props => props.active ? '#0366d6' : 'transparent'};
  color: ${props => props.active ? 'white' : '#586069'};
  border: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  border-radius: 6px 6px 0 0;
  
  &:hover {
    background: ${props => props.active ? '#0366d6' : '#f6f8fa'};
  }
`;

const StatusIndicator = styled.div`
  padding: 8px;
  margin-bottom: 15px;
  border-radius: 4px;
  font-size: 12px;
  background-color: ${props => props.connected ? '#e6ffed' : '#ffeef0'};
  color: ${props => props.connected ? '#22863a' : '#cb2431'};
  border: 1px solid ${props => props.connected ? '#34d058' : '#f97583'};
`;

// --- (NOVO) Componente de Mensagem ---
const Message = styled.div`
  padding: 10px;
  margin-top: 15px;
  border-radius: 6px;
  font-size: 14px;
  background-color: ${props => (props.type === 'error' ? '#ffeef0' : '#e6ffed')};
  color: ${props => (props.type === 'error' ? '#cb2431' : '#22863a')};
  border: 1px solid ${props => (props.type === 'error' ? '#f97583' : '#34d058')};
`;


function App() {
  // --- (MUDANA) Aba padro agora  'ingestao' ---
  const [activeTab, setActiveTab] = useState('ingestao');
  const [backendStatus, setBackendStatus] = useState(null);
  
  // Estado da Consulta
  const [consultaResultado, setConsultaResultado] = useState(null);
  const [consultaLoading, setConsultaLoading] = useState(false);
  const [consultaError, setConsultaError] = useState(null);

  // --- (NOVO) Estado da Ingesto ---
  const [ingestLoading, setIngestLoading] = useState(false);
  const [ingestError, setIngestError] = useState(null);
  const [ingestSuccess, setIngestSuccess] = useState(null);

  useEffect(() => {
    // (A verificao de conexo continua igual)
    const verificarConexao = async () => {
      try {
        const resposta = await testarConexao();
        setBackendStatus(resposta.status === 'online');
      } catch (erro) {
        console.error('Erro ao verificar conexo:', erro);
        setBackendStatus(false);
      }
    };
    verificarConexao();
  }, []);

  // Handler da Consulta (igual ao que voc tinha)
  const handleConsulta = async (dados) => {
    setConsultaLoading(true);
    setConsultaError(null);
    setConsultaResultado(null); // Limpa resultado anterior
    
    try {
      const resposta = await consultarAPI(dados);
      setConsultaResultado(resposta);
    } catch (erro) {
      console.error('Erro na consulta:', erro);
      setConsultaError('Erro ao processar consulta. Verifique a conexo e tente novamente.');
    } finally {
      setConsultaLoading(false);
    }
  };

  // --- (NOVO) Handler da Ingesto ---
  const handleIngestao = async (dados) => {
    setIngestLoading(true);
    setIngestError(null);
    setIngestSuccess(null);
    
    try {
      // Chama a nova funo da API
      const resposta = await ingestarRepositorio(dados);
      setIngestSuccess(resposta.mensagem || 'Ingesto enfileirada com sucesso!');
    } catch (erro) {
      console.error('Erro na ingesto:', erro);
      setIngestError('Erro ao iniciar ingesto. Verifique o backend.');
    } finally {
      setIngestLoading(false);
    }
  };

  return (
    <AppContainer>
      <Header />
      
      {backendStatus !== null && (
        <StatusIndicator connected={backendStatus}>
          {backendStatus 
            ? ' Conectado ao backend' 
            : ' No foi possvel conectar ao backend'}
        </StatusIndicator>
      )}
      
      <Tabs>
        {/* --- (NOVA ABA) --- */}
        <Tab 
          active={activeTab === 'ingestao'} 
          onClick={() => setActiveTab('ingestao')}
        >
          Ingesto
        </Tab>
        <Tab 
          active={activeTab === 'consulta'} 
          onClick={() => setActiveTab('consulta')}
        >
          Consulta
        </Tab>
        <Tab 
          active={activeTab === 'relatorio'} 
          onClick={() => setActiveTab('relatorio')}
        >
          Relatrio
        </Tab>
      </Tabs>
      
      {/* --- (NOVO) Contedo da Aba Ingesto --- */}
      {activeTab === 'ingestao' && (
        <>
          <IngestaoForm onSubmit={handleIngestao} loading={ingestLoading} />
          {ingestSuccess && <Message type="success">{ingestSuccess}</Message>}
          {ingestError && <Message type="error">{ingestError}</Message>}
        </>
      )}

      {activeTab === 'consulta' && (
        <>
          <ConsultaForm onSubmit={handleConsulta} loading={consultaLoading} />
          {consultaResultado && <ResultadoConsulta resultado={consultaResultado} />}
          {consultaError && <Message type="error">{consultaError}</Message>}
        </>
      )}
      
      {activeTab === 'relatorio' && (
        <RelatorioForm />
      )}
    </AppContainer>
  );
}

export default App;